<template>
	<Example vertical id="example-tag-hot-tags">
		<template slot="source">
			<div class="example-tag-hot-tags">
				<h4>Categories:</h4>
				<vui-tag
					v-for="tag in tags"
					color="primary"
					checkable
					:key="tag"
					:checked="selectedTags.indexOf(tag) > -1"
					@change="checked => handleChange(checked, tag)"
				>
					{{tag}}
				</vui-tag>
			</div>
		</template>
		<template slot="title">热门标签</template>
		<template slot="describe">
			<p>选择你感兴趣的话题。</p>
		</template>
		<template slot="code">{{code}}</template>
	</Example>
</template>

<script>
	import Example from "@/components/example";
	import code from "./code";

	export default {
		name: "ExampleTagHotTags",
		components: {
			Example
		},
		data() {
			return {
				code,
				tags: ["Movies", "Books", "Music", "Sports"],
				selectedTags: ["Movies"]
			};
		},
		methods: {
			handleChange(checked, tag) {
				let selectedTags = this.selectedTags;
				let nextSelectedTags = checked ? [...selectedTags, tag] : selectedTags.filter(item => item !== tag);

				this.selectedTags = nextSelectedTags;

				console.log(this.selectedTags);
			}
		}
	};
</script>

<style>
	.example-tag-hot-tags:before,
	.example-tag-hot-tags:after { content:""; display:block; width:100%; height:0; visibility:hidden; clear:both; }
	.example-tag-hot-tags > h4,
	.example-tag-hot-tags > .vui-tag { float:left; margin-right:10px; }
	.example-tag-hot-tags > h4 { color:#333; }
</style>


